import React, { useEffect, useState } from "react";

import Logo from "../assets/images/icon/Sony_logo.svg";

const Header = () => {
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 100);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <header className={scrolled ? "menu-bg-visible" : ""}>
      <div className="menu-bg"></div>
      <div className="inner">
        <nav id="nav">
          <h1 className="logo">
            <a href="/">
              <img src={ Logo } alt="Sony Logo" />
            </a>
          </h1>
          <ul className="menu">
            <li>
              
              <div className="submenu">
                <p></p>
                <p> </p>
                <p></p>
                <p></p>
              </div>
            </li>
            <li>
              
              <div className="submenu">
                <p> </p>
                <p>  </p>
                <p> </p>
                <p> </p>
              </div>
            </li>
            <li>
               
              <div className="submenu">
                <p></p>
                <p>FAQ</p>
                <p> </p>
                <p> </p>
              </div>
            </li>
            <li>
              
              <div className="submenu">
                <p> </p>
                <p> </p>
                <p> </p>
                <p> </p>
              </div>
            </li>
            <li>
               
              <div className="submenu">
                <p></p>
                <p> </p>
                <p></p>
                <p></p>
                <p> </p>
              </div>
            </li>
          </ul>

          <div id="Infomenu">
            <span><a href="#"></a></span>
            <span><a href="#"></a></span>
            <span><a href="#"></a></span>
          </div>

          <div id="tm-menu">
            <span><a href="#"></a></span>
            <span><a href="#"></a></span>
          </div>
        </nav>
      </div>
    </header>
  );
};

export default Header;
